html {
  font-size: 16px; }
  @media (min-width: 500px) {
    html {
      font-size: 18px; } }
  @media (min-width: 720px) {
    html {
      font-size: 21px; } }

body {
  font-family: 'Exo', sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5em;
  color: #233d4d;
  margin: 5%; }
  @media (min-width: 980px) {
    body {
      max-width: 980px;
      margin: 5% auto;
      padding: 0 5%; } }

h1, h2, h3, h4, h5, h6 {
  max-width: 10em;
  font-weight: 900;
  line-height: 1em;
  color: #233d4d; }

h1 {
  font-size: 3rem; }

h2 {
  font-size: 2.4rem; }

h3 {
  font-size: 2rem;
  color: #fe7f2d; }

h4 {
  font-size: 1.6rem; }

h5 {
  font-size: 1.4rem; }

h6 {
  font-size: 1rem; }

ul {
  padding-left: 1em; }

a {
  font-size: 0.85em;
  color: #233d4d;
  background-color: #fcca46;
  border-radius: 0.3em;
  padding: 0.15em 0.4em;
  text-decoration: none; }
  a:hover {
    background-color: #fe7f2d; }
  a .svg-inline {
    display: inline-block;
    vertical-align: middle;
    width: 1em;
    height: 0.85em;
    margin-right: 0.35em; }
