.site-header {
  display: flex;
  text-align: right;
  align-items: flex-end;
  justify-content: flex-end;
  flex-direction: column; }
  @media (min-width: 720px) {
    .site-header {
      flex-direction: row; } }
  .site-header h1 {
    transform: rotate(1deg);
    line-height: 0.85em;
    color: #fcca46;
    text-align: right; }
  .site-header .i {
    display: inline-block;
    transform: rotate(-2.5deg);
    color: #fe7f2d; }
  .site-header .dot {
    display: inline-block;
    transform: rotate(2.5deg);
    color: #233d4d; }
  .site-header .profile-pic {
    position: relative;
    width: 200px;
    height: 200px;
    margin-right: 0;
    transform: rotate(-10deg);
    transition: all 0.3s ease;
    overflow: hidden;
    border: 0.2em solid white;
    border-radius: 1rem; }
    .site-header .profile-pic img {
      position: absolute;
      left: calc(50% - 125px);
      top: calc(50% - 125px);
      width: 250px;
      transform: rotate(10deg);
      transition: all 0.3s ease; }
  .site-header:hover .profile-pic, .site-header:hover .profile-pic img {
    transform: rotate(0deg);
    transition: all 0.3s ease; }
