.curriculum-vitae__job {
  margin-bottom: 1.5em; }
  @media (min-width: 720px) {
    .curriculum-vitae__job {
      display: flex; } }
  .curriculum-vitae__job dt {
    flex: 0 0 7em; }
  .curriculum-vitae__job dd {
    margin-left: 0; }
  .curriculum-vitae__job-company, .curriculum-vitae__job-title {
    display: block;
    margin-top: 0;
    margin-bottom: 0.5em; }
  .curriculum-vitae__job-company {
    font-weight: bold; }
  .curriculum-vitae__job-description ul {
    margin-bottom: 0.5em; }
